const createHTMLElement = (data) => {
  return `
  <img class="card_thumb" src="${data["pictureId"]}" alt="${data["name"]}" title="${data["name"]}">
            <div class="city">${data["city"]}</div>
            <div class="card-content">
                <div class="card-rating">
                    <i class='bx bxs-star' ></i>
                    <a href="#" class="card-rating-value">${data["rating"]}</a>
                </div>
                <h3 class="card-title">${data["name"]}</h3>
                <div class="card-desc">${data["description"]}</div>
            </div>
            <button class="button-search button-read-more">Read More</button>
  `;
};

const renderRestaurant = (datas) => {
  const createElement = document.querySelector(".cards-box");
  createElement.innerHTML = "";

  datas.forEach(function (data) {
    const cardElement = document.createElement("div");
    cardElement.classList.add("card");
    cardElement.innerHTML = createHTMLElement(data);

    const buttonReadMore = cardElement.querySelector(".button-read-more");
    const cardDesc = cardElement.querySelector(".card-desc");
    buttonReadMore.addEventListener("click", (event) => {
      event.preventDefault();

      cardDesc.classList.toggle("active");

      buttonReadMore.textContent = cardDesc.classList.contains("active")
        ? "Close"
        : "Read More";
    });

    createElement.appendChild(cardElement);
  });
};

const renderError = () => {
  const createElement = document.querySelector(".cards-box");
  createElement.innerHTML = '<p class="error">Restaurant Not Found!</p>';
};

const fetchRestaurant = () => {
  import("../public/data/DATA.json").then(({ default: jsonData }) => {
    console.log(jsonData);
    let datas = jsonData["restaurants"];
    renderRestaurant(datas);
  });
};

const searchRestaurant = (datas, keyword) => {
  const filteredRestaurants = datas.filter((data) => {
    const cityColumn = data.city.toLowerCase();
    const keywordLower = keyword.toLowerCase();

    return cityColumn.includes(keywordLower);
  });

  if (filteredRestaurants.length) {
    renderRestaurant(filteredRestaurants);
  } else {
    renderError();
  }

  const jumbotron = document.getElementById("jumbotron");
  jumbotron.innerHTML = "";
};

const fetchSearchRestaurant = (keyword) => {
  import("../public/data/DATA.json").then(({ default: jsonData }) => {
    console.log(jsonData);
    let datas = jsonData["restaurants"];
    searchRestaurant(datas, keyword);
  });
};

const buttonSearch = document.getElementById("button-search");
const buttonDropdown = document.getElementById("button-dropdown");
const handleSearch = (event, idInput) => {
  event.preventDefault();

  const keyword = document.getElementById(idInput).value;
  if (keyword.length > 0) {
    const queryString = `?keyword=${encodeURIComponent(keyword)}`;
    window.location.href = window.location.origin + queryString;
  } else {
    alert("Fill search column.");
  }
};

buttonSearch.addEventListener("click", () => {
  handleSearch(event, "input-search");
});

buttonDropdown.addEventListener("click", () => {
  handleSearch(event, "input-search-dropdown");
});

document.addEventListener("DOMContentLoaded", () => {
  const queryString = window.location.search;
  const urlParams = new URLSearchParams(queryString);
  const keyword = urlParams.get("keyword");

  if (keyword == null) {
    fetchRestaurant();
  } else {
    fetchSearchRestaurant(keyword);
  }
});
