  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
}

:root {
  --primary-color: #fff;
  --color-1: #d6ccc2;
  --color-2: #ded6ce;
  --color-3: #e5ded8;
  --color-4: #eeeae6;
  --color-5: #f5ebe0;
  --color-6: #e3d5ca;
  --text-color-1: #20201d;
  --text-color-2: #fff;
  --text-color-1-hover: #b7965c;
  --border-input: #20201d;
  --border-input-focus: #b7965c;
  --button-color: #b7965c;
  --button-color-hover: #af7d27;
  --form-control-border: #cccc;
}

body {
  background-color: var(--primary-color);
}

/* ============= NAVBAR =========== */
header {
  position: sticky;
  top: 0;
  z-index: 9999;
  padding: 1rem 1rem;
  background-color: var(--primary-color);
}

nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo,
.nav-menu a,
.dropdown-menu a {
  text-decoration: none;
}

.nav-menu a,
.dropdown-menu a {
  text-decoration: none;
  padding: 0.7rem 0;
}

.logo {
  width: 8rem;
}

.hamburger-menu {
  padding: 0.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

.hamburger-menu i {
  color: var(--button-color);
}

.nav-menu {
  display: none;
  gap: 1rem;
}

.nav-menu a {
  font-size: 1rem;
  color: var(--text-color-1);
  transition: all 0.2s;
}

.nav-menu a:hover {
  color: var(--text-color-1-hover);
}

.form-search {
  display: none;
}

.form-search {
  justify-content: end;
  gap: 0.5rem;
  width: 100%;
}

.form-control {
  width: 100%;
  padding: 0.8rem 1rem;
  font-size: 0.9rem;
  border-radius: 0.2rem;
  border: 1px solid var(--form-control-border);
  outline: none;
}

.form-control:focus {
  border-color: var(--border-input-focus);
  box-shadow: 0 0 5px rgba(183, 150, 92, 0.5);
}

.button-search {
  padding: 0.8rem 1rem;
  font-size: 0.9rem;
  color: var(--text-color-2);
  background-color: var(--button-color);
  border: none;
  outline: none;
  border-radius: 0.2rem;
  cursor: pointer;
  transition: all 0.2s;
}

.button-search:hover {
  background-color: var(--button-color-hover);
}

.dropdown-menu {
  position: absolute;
  padding: 2rem 1rem;
  top: 1000px;
  left: 1rem;
  right: 1rem;
  background-color: var(--button-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  border-radius: 0.5rem;
  opacity: 0;
  max-height: 0;
  overflow: hidden;
  transition: opacity 0.3s ease, max-height 0.3s ease;
}

.dropdown-menu.active {
  top: 95px;
  opacity: 1;
  max-height: 500px;
}

.dropdown-menu a {
  color: var(--text-color-2);
  transition: all 0.2s;
}

.dropdown-menu a:hover {
  color: var(--color-5);
}

.form-dropdown {
  display: flex;
}

.button-dropdown {
  background-color: var(--text-color-2);
  color: var(--button-color);
}

.button-dropdown:hover {
  background-color: var(--color-5);
}

/* ====== JUMBOTRON ====== */

.jumbotron {
  width: 100%;
  height: 80vh;
  background-size: cover;
  background-position: center;
}

.jumbotron-overlay {
  padding: 1rem 1rem;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.jumbotron-title {
  color: var(--text-color-2);
  text-align: center;
  font-size: 3.5rem;
}

.jumbotron-subtitle {
  color: var(--text-color-2);
  text-align: center;
  font-size: 1rem;
}

.restaurant-list {
  padding: 3rem 1rem;
  width: 100%;
}

.restaurant-list h2 {
  text-align: center;
  color: var(--button-color);
  margin-bottom: 2rem;
  font-size: 2rem;
}

.cards-box {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
}

.cards-box .error {
  font-size: 1.2rem;
  color: var(--text-color-1);
  font-style: italic;
  font-weight: 300;
}

.card {
  height: fit-content;
  position: relative;
  width: 100%;
  padding: 1rem;
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.card img {
  width: 100%;
  height: 12rem;
  object-fit: cover;
  border-radius: 0.2rem;
}

.city {
  position: absolute;
  top: 1rem;
  left: 1rem;
  border-radius: 0.2rem 0 0.2rem 0;
  background-color: var(--button-color);
  padding: 5px 10px 5px;
  color: white;
  font-weight: 500;
}

.card-rating {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 0.3rem;
}

.card-rating i {
  color: var(--button-color);
  font-size: 1.2rem;
}

.card-rating a {
  text-decoration: none;
  font-size: 1rem;
  color: var(--text-color-1);
}

.card-title {
  color: var(--text-color-1);
  font-size: 1.5rem;
}

.card-desc {
  color: var(--text-color-1);
  font-size: 1rem;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.card-desc.active {
  display: unset;
  -webkit-line-clamp: unset;
  -webkit-box-orient: unset;
  overflow: unset;
}

.button-read-more {
  margin-top: 1rem;
}

/* ====== FOOTER ====== */

footer {
  padding: 0 1rem;
}

.footer-box {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: space-between;
}

.footer-card {
  width: 100%;
}

.footer-card img {
  width: 8rem;
  margin-bottom: 0.5rem;
}

.footer-card p {
  line-height: 1.5rem;
  font-size: 1rem;
  color: var(--text-color-1);
}

.footer-desc {
  width: 20rem;
}

.footer-nav-menu {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.footer-nav-menu h4 {
  font-size: 1.4rem;
  color: var(--button-color);
}

.footer-nav-menu a {
  padding: 0.7rem 0.7rem 0.7rem 0;
  font-size: 1rem;
  font-weight: 400;
  color: var(--text-color-1);
  text-decoration: none;
  transition: all 0.2s;
}

.footer-nav-menu a:hover {
  color: var(--button-color-hover);
}

.footer-nav-menu p {
  padding: 0.7rem 0.7rem 0.7rem 0;
  font-size: 1rem;
  font-weight: 400;
}

.copyright {
  font-size: 1rem;
  font-weight: 400;
  padding: 1rem;
  text-align: center;
}

.skip-content {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: var(--button-color);
  border: none;
  border-radius: 0 0 0.3rem 0;
  outline: none;
  text-decoration: none;
  color: white;
  padding: 8px;
  z-index: 99999;
}

.skip-content:focus {
  top: 0;
}

/* ====== RESPONSIVE ====== */

@media screen and (min-width: 576px) {
  header {
    padding: 1rem 2rem;
  }

  .form-search {
    width: 20rem;
  }

  .jumbotron-overlay {
    padding: 1rem 2rem;
  }

  .restaurant-list {
    padding: 3rem 2rem;
    width: 100%;
  }

  .cards-box {
    grid-template-columns: 1fr 1fr;
  }

  .restaurant-list h2 {
    font-size: 1.8rem;
    margin-bottom: 3rem;
  }

  footer {
    padding: 0 2rem;
  }

  .footer-box {
    flex-direction: row;
  }
}

@media screen and (min-width: 992px) {
  header {
    padding: 1rem 10rem;
  }

  .nav-menu {
    display: flex;
    gap: 1rem;
  }

  .form-search {
    display: flex;
  }

  .hamburger-menu {
    display: none;
  }

  .dropdown-menu {
    display: none;
  }

  .jumbotron-overlay {
    padding: 1rem 10rem;
  }

  .restaurant-list {
    padding: 4rem 10rem;
    width: 100%;
  }

  .cards-box {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2rem;
  }

  .restaurant-list h2 {
    font-size: 2rem;
    margin-bottom: 3rem;
  }

  footer {
    padding: 0 10rem;
  }

  .footer-box {
    flex-direction: row;
  }
}

@media screen and (min-width: 1200px) {
  header {
    padding: 1rem 0;
  }

  nav {
    width: 1000px;
    margin: 0 auto;
  }

  .jumbotron {
    width: 1000px;
    margin: 0 auto;
    border-radius: 0.5rem;
    overflow: hidden;
  }

  .jumbotron-overlay {
    padding: 1rem 0rem;
  }

  .restaurant-list {
    padding: 3rem 0;
    width: 1000px;
    margin: 0 auto;
  }

  footer {
    width: 1000px;
    margin: 0 auto;
    padding: 0;
  }
}
