t-size: 0.9rem;\n  border-radius: 0.2rem;\n  border: 1px solid var(--form-control-border);\n  outline: none;\n}\n\n.form-control:focus {\n  border-color: var(--border-input-focus);\n  box-shadow: 0 0 5px rgba(183, 150, 92, 0.5);\n}\n\n.button-search {\n  padding: 0.8rem 1rem;\n  font-size: 0.9rem;\n  color: var(--text-color-2);\n  background-color: var(--button-color);\n  border: none;\n  outline: none;\n  border-radius: 0.2rem;\n  cursor: pointer;\n  transition: all 0.2s;\n}\n\n.button-search:hover {\n  background-color: var(--button-color-hover);\n}\n\n.dropdown-menu {\n  position: absolute;\n  padding: 2rem 1rem;\n  top: 1000px;\n  left: 1rem;\n  right: 1rem;\n  background-color: var(--button-color);\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  gap: 1rem;\n  border-radius: 0.5rem;\n  opacity: 0;\n  max-height: 0;\n  overflow: hidden;\n  transition: opacity 0.3s ease, max-height 0.3s ease;\n}\n\n.dropdown-menu.active {\n  top: 95px;\n  opacity: 1;\n  max-height: 500px;\n}\n\n.dropdown-menu a {\n  color: var(--text-color-2);\n  transition: all 0.2s;\n}\n\n.dropdown-menu a:hover {\n  color: var(--color-5);\n}\n\n.form-dropdown {\n  display: flex;\n}\n\n.button-dropdown {\n  background-color: var(--text-color-2);\n  color: var(--button-color);\n}\n\n.button-dropdown:hover {\n  background-color: var(--color-5);\n}\n\n/* ====== JUMBOTRON ====== */\n\n.jumbotron {\n  width: 100%;\n  height: 80vh;\n  background-size: cover;\n  background-position: center;\n}\n\n.jumbotron-overlay {\n  padding: 1rem 1rem;\n  width: 100%;\n  height: 100%;\n  background-color: rgba(0, 0, 0, 0.5);\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  flex-direction: column;\n}\n\n.jumbotron-title {\n  color: var(--text-color-2);\n  text-align: center;\n  font-size: 3.5rem;\n}\n\n.jumbotron-subtitle {\n  color: var(--text-color-2);\n  text-align: center;\n  font-size: 1rem;\n}\n\n.restaurant-list {\n  padding: 3rem 1rem