direction: column;\n  gap: 0.5rem;\n}\n\n.footer-nav-menu h4 {\n  font-size: 1.4rem;\n  color: var(--button-color);\n}\n\n.footer-nav-menu a {\n  padding: 0.7rem 0.7rem 0.7rem 0;\n  font-size: 1rem;\n  font-weight: 400;\n  color: var(--text-color-1);\n  text-decoration: none;\n  transition: all 0.2s;\n}\n\n.footer-nav-menu a:hover {\n  color: var(--button-color-hover);\n}\n\n.footer-nav-menu p {\n  padding: 0.7rem 0.7rem 0.7rem 0;\n  font-size: 1rem;\n  font-weight: 400;\n}\n\n.copyright {\n  font-size: 1rem;\n  font-weight: 400;\n  padding: 1rem;\n  text-align: center;\n}\n\n.skip-content {\n  position: absolute;\n  top: -40px;\n  left: 0;\n  background-color: var(--button-color);\n  border: none;\n  border-radius: 0 0 0.3rem 0;\n  outline: none;\n  text-decoration: none;\n  color: white;\n  padding: 8px;\n  z-index: 99999;\n}\n\n.skip-content:focus {\n  top: 0;\n}\n\n/* ====== RESPONSIVE ====== */\n\n@media screen and (min-width: 576px) {\n  header {\n    padding: 1rem 2rem;\n  }\n\n  .form-search {\n    width: 20rem;\n  }\n\n  .jumbotron-overlay {\n    padding: 1rem 2rem;\n  }\n\n  .restaurant-list {\n    padding: 3rem 2rem;\n    width: 100%;\n  }\n\n  .cards-box {\n    grid-template-columns: 1fr 1fr;\n  }\n\n  .restaurant-list h2 {\n    font-size: 1.8rem;\n    margin-bottom: 3rem;\n  }\n\n  footer {\n    padding: 0 2rem;\n  }\n\n  .footer-box {\n    flex-direction: row;\n  }\n}\n\n@media screen and (min-width: 992px) {\n  header {\n    padding: 1rem 10rem;\n  }\n\n  .nav-menu {\n    display: flex;\n    gap: 1rem;\n  }\n\n  .form-search {\n    display: flex;\n  }\n\n  .hamburger-menu {\n    display: none;\n  }\n\n  .dropdown-menu {\n    display: none;\n  }\n\n  .jumbotron-overlay {\n    padding: 1rem 10rem;\n  }\n\n  .restaurant-list {\n    padding: 4rem 10rem;\n    width: 100%;\n  }\n\n  .cards-box {\n    grid-template-columns: 1fr 1fr 1fr;\n    gap: 2rem;\n  }\n\n  .restaurant-list h2 {\n    font-size: 2rem;\n    margin-bottom: 3rem;\n  }\n\n  footer {\n    padding: 0 10rem;\n  }\n\n  .footer-box {\n    flex-direction: row;\n  }\n}\n\n@media screen and (min-width: 1200px) {\n  header {\n    padding: 1rem 0;\n  }\n\n  nav {\n    width: 1000px;\n    margin: 0 auto;\n  }\n\n  .jumbotron {\n    width: 1000px;\n    margin: 0 auto;\n    border-radius: 0.5rem;\n    overflow: hidden;\n  }\n\n  .jumbotron-overlay {\n    padding: 1rem 0rem;\n  }\n\n  .restaurant-list {\n    padding: 3rem 0;\n    width: 1000px;\n    margin: 0 auto;\n  }\n\n  footer {\n    width: 1000px;\n    margin: 0 auto;\n    padding: 0;\n  }\n}\n"],"sourceRoot":""}]);
